body {
  text-align: center;
}

.phonebookcontainer {
  font-family: serif;
}
button {
  padding: 3px px;
  background-color: rgb(187, 61, 82);
  font-size: 1rem;
  margin-right: 12px;
  border: none;
  border-radius: 5px;
  margin-top: 4px;
}
.printInput {
  padding: 1rem;
}
input {
  margin-left: 5px;
  margin-top: 3px;
}
table {
  box-sizing: border-box;
  margin: 4rem auto;
  width: 60%;
  box-sizing: border-box;
  margin: 3rem auto;
  text-align: center;
}

tr th {
  background: #1d6c6f;
  color: rgba(14, 14, 20, 0.77);
  font-size: 1rem;
  padding: 0.6rem;
  color: white;
  border-bottom: 2px solid rgb(2, 14, 11);
  border-top: 4px solid rgb(2, 14, 11);
  border-left: 4px solid rgb(2, 14, 11);
}
tr {
  border-collapse: collapse;
  font-weight: bold;
  width: fit-content;
}
tr th:last-child {
  border-right: 4px solid rgb(2, 14, 11);
}
tr td {
  color: rgba(13, 13, 14, 0.77);
  padding: 0.5rem;
  border-top: 2px solid rgb(2, 12, 11);
  border-left: 3px solid rgb(2, 14, 11);
  background: rgb(129, 150, 93);
}

/* .dml_table tr th:last-child, */
tr td:last-child {
  border-right: 3px solid rgb(1, 11, 9);
}
/* .dml_table tr:last-child th, */
tr:last-child td {
  border-bottom: 2px solid rgb(1, 11, 9);
}

tr td {
  font-weight: bold;
}

.error {
  color: rgb(21, 150, 57);
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
